<template>
    <ul class="nav-list">
        <li class="nav-item" v-for="(item,index) in items">
            <router-link :to="'/' + item.ad">{{item.name}}</router-link>
        </li>
    </ul>
</template>

<script>
    export default {
        data () {
            return {
                items: [
                    {name:'技术博客',ad:'personBlog'},
                    {name:'AngularJs',ad:'angular'},
                    {name:'NodeJs',ad:'nodejs'},
                    {name:'VueJs & React',ad:'vuereact'},
                    {name:'Javascript',ad:'javascript'},
                    {name:'移动端开发',ad:'mobile'},
                    {name:'构建工具',ad:'maketool'},
                    {name:'工具手册',ad:'handbook'},
                    {name:'静态资源',ad:'quietResource'},
                    {name:'版本控制',ad:'gits'},
                    {name:'关于本站',ad:'aboutme'}
                ]
            }
        }
    }
</script>

<style scoped>
    .nav-list{
        width: 200px;
        position: absolute;;
        top: 80px;
        left: 20px;
        margin-top: 20px;
        border-top: 1px solid #1BA265;
    }
    .nav-item{
        list-style: none;
        height: 40px;
        line-height: 40px;
        position: relative;
        text-align: center;
        cursor: pointer;
        margin-bottom: 4px;
        border-bottom: 1px solid #eee;
    }
    .nav-item a{
        display: block;
        text-decoration: none;
        color: #333;
    }
    .nav-item a:hover{
        background-color: #10A0EA;
        cursor: pointer;
        color: #fff;
    }
    a.router-link-active{
        color:#10A0EA;
        font-weight: bold;
    }
    a.router-link-active:before{
        position: absolute;
        left: 0;
        bottom: 0;
        display: block;
        content: '';
        width: 4px;
        height: 40px;
        background-color: #10A0EA;
    }
</style>



